"use client";

import {
  createContext,
  useContext,
  useState,
  useEffect,
  useCallback,
} from "react";
import type { ClinicSetting } from "@/types/clinic-setting";
import clinicSettingService from "@/services/clinic-setting.service";

interface ClinicSettingsContextType {
  settings: ClinicSetting | null;
  isLoading: boolean;
  error: string | null;
  refetch: () => Promise<void>;
}

const ClinicSettingsContext = createContext<ClinicSettingsContextType | undefined>(
  undefined
);

export function ClinicSettingsProvider({ children }: { children: React.ReactNode }) {
  const [settings, setSettings] = useState<ClinicSetting | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchSettings = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);

      // Try to get from localStorage first for faster initial load
      const cached = localStorage.getItem("clinic_settings");
      if (cached) {
        try {
          const parsedCache = JSON.parse(cached);
          setSettings(parsedCache);
        } catch {
          // Invalid cache, ignore
        }
      }

      // Fetch from API
      const response = await clinicSettingService.getSettings();
      if (response.success && response.data) {
        setSettings(response.data);
        // Cache the settings
        localStorage.setItem("clinic_settings", JSON.stringify(response.data));
      }
    } catch (err) {
      console.error("Failed to fetch clinic settings:", err);
      setError("Gagal memuat pengaturan klinik");
      // Keep using cached data if available
    } finally {
      setIsLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchSettings();
  }, [fetchSettings]);

  const refetch = useCallback(async () => {
    await fetchSettings();
  }, [fetchSettings]);

  return (
    <ClinicSettingsContext.Provider
      value={{
        settings,
        isLoading,
        error,
        refetch,
      }}
    >
      {children}
    </ClinicSettingsContext.Provider>
  );
}

export function useClinicSettings() {
  const context = useContext(ClinicSettingsContext);
  if (context === undefined) {
    throw new Error(
      "useClinicSettings must be used within a ClinicSettingsProvider"
    );
  }
  return context;
}
